.backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,0.9);
  width: 60%;
  height: 60%;
  font-size: 1.8rem;
  padding: 20%;
}

.modal {
  background-color: #091e0e;
  border: 10px solid #040a04;
  border-radius: 25px;
  margin: 0 auto;
  padding: 50px;
}

.modal-game-time-title {
  padding: 10px 10px 20px;
  font-size: 2.2rem;
  font-weight: 200;
}

.modal-button {
  margin: 40px 20px;
  background-color: #040a04;
  display: inline-block;
  padding: 10px 40px;
  border: 2px solid #000000;
  border-radius: 10px;
}

.modal-button:hover {
  cursor: pointer;
  background-color: #092d09;
}


