.game-wrapper {
  border: 1px solid #021102;
  border-radius: 25px;
  background-color: #09370f;
  margin: 25px;
  padding: 15px;
  -webkit-box-shadow: inset 0 0 10px 3px #000000;
          box-shadow: inset 0 0 10px 3px #000000;
}

.game-wrapper > div {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.card-wrapper {
  width: 180px;
  height: 250px;
  border: 1px solid #082100;
  border-radius: 15px;
  cursor: pointer;
  margin: 15px;
  position: relative;
  -webkit-box-shadow: 0 0 10px 3px #000000;
          box-shadow: 0 0 10px 3px #000000;
}

.card-wrapper > div {
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: -webkit-transform .3s linear 0s;
  transition: -webkit-transform .3s linear 0s;
  -o-transition: transform .3s linear 0s;
  transition: transform .3s linear 0s;
  transition: transform .3s linear 0s, -webkit-transform .3s linear 0s;
  width: 180px;
  height: 250px;
  border-radius: 15px;
}

.card-wrapper .card-back {
  -webkit-transform: perspective(750px) rotateY(0deg);
          transform: perspective(750px) rotateY(0deg);
}

.card-wrapper .card-front {
  -webkit-transform: perspective(750px) rotateY(180deg);
          transform: perspective(750px) rotateY(180deg);
}

.card-wrapper.rotate .card-back {
  -webkit-transform: perspective(750px) rotateY(-180deg) !important;
          transform: perspective(750px) rotateY(-180deg) !important;
}

.card-wrapper.rotate .card-front {
  -webkit-transform: perspective(750px) rotateY(0deg) !important;
          transform: perspective(750px) rotateY(0deg) !important;
}



