.rules-wrapper {
  border: 1px solid #021102;
  border-radius: 25px;
  background-color: #09370f;
  margin: 25px;
  padding: 15px;
  -webkit-box-shadow: inset 0 0 10px 3px #000000;
          box-shadow: inset 0 0 10px 3px #000000;
}

.rules-title {
  font-size: 2.5rem;
  padding: 20px;
}

.rules {
  font-size: 1.5rem;
  text-align: left;
  padding: 15px;
  font-style: italic;
}

.rules > li {
  padding: 10px;
}


