html {
  font-size: 20px;
}

html, body {
  width: 100%;
}
.test {
  background-color: #222222;
}

body {
  margin: 0 auto;
  background-color: #060f06;
  text-align: center;
  font-family: 'Roboto', 'sans-serif';
  font-weight: 100;
  color: #57ae00;
}

.wrapper {
  margin: 50px auto;
  border: 2px solid #040904;
  border-radius: 25px;
  background-color: #091e0e;
  min-width: 640px;
  max-width: 1500px;
  padding: 50px;
  -webkit-box-shadow: 0 0 10px 5px #000000;
          box-shadow: 0 0 10px 5px #000000;
}


