.navigation {
  font-size: 1.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  text-align: center;

  border: 1px solid #021102;
  border-radius: 0 0 25px 25px;
  background-color: #082308;
  margin: 0 25px 25px;
  /*padding: 15px;*/
  -webkit-box-shadow:
  2px 2px 10px 1px #000000,
  -2px 2px 10px 1px #000000;
          box-shadow:
  2px 2px 10px 1px #000000,
  -2px 2px 10px 1px #000000;
}

.navigation > * {
  border: 1px solid #021102;
  margin: 0;
  width: 100%;
  padding: 15px;
  text-align: center;
}

.navigation:first-child {
  color: red;
  background-color: red;
}

.nav-menu {
  background-color: #09370f;
  border-radius: 0 0 0 25px;
}

.start-game {
  border-radius: 0 0 25px 0;
}

.nav-button {
  cursor: pointer;
  -webkit-transition: background-color 1.2s;
  -o-transition: background-color 1.2s;
  transition: background-color 1.2s;
  text-decoration: none;
  color: #57ae00;
}

.nav-button.active,
.nav-button:hover {
  background-color: #094b0f;
  -webkit-transition: background-color 0.4s;
  -o-transition: background-color 0.4s;
  transition: background-color 0.4s;
}

.active-nav-button {
  background-color: #097a14;
}


